.tu-time-panel {
  margin: 5px 0;
  border-radius: 2px;
  position: absolute;
  width: 180px;
  left: 0;
  z-index: 1000;
  user-select: none;
  box-sizing: content-box;
  color: #8394be;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-time-panel__content {
    font-size: 0;
    position: relative;
    overflow: hidden; }
    .tu-time-panel__content::after, .tu-time-panel__content::before {
      content: "";
      top: 50%;
      position: absolute;
      margin-top: -14px;
      height: 32px;
      z-index: -1;
      left: 0;
      right: 0;
      box-sizing: border-box;
      padding-top: 6px;
      text-align: left;
      border-top: 1px solid rgba(190, 200, 228, 0.38);
      border-bottom: 1px solid rgba(190, 200, 228, 0.38); }
    .tu-time-panel__content::after {
      left: 50%;
      margin-left: 12%;
      margin-right: 12%; }
    .tu-time-panel__content::before {
      padding-left: 50%;
      margin-right: 12%;
      margin-left: 12%; }
    .tu-time-panel__content.has-seconds::after {
      left: calc(100% / 3 * 2); }
    .tu-time-panel__content.has-seconds::before {
      padding-left: calc(100% / 3); }
    .tu-time-panel__content.is-arrow::after, .tu-time-panel__content.is-arrow::before {
      margin-top: -15px; }
  .tu-time-panel__footer {
    border-top: 1px solid rgba(190, 200, 228, 0.38);
    padding: 4px;
    height: 36px;
    line-height: 25px;
    text-align: right;
    box-sizing: border-box; }
  .tu-time-panel__btn {
    margin-left: 8px; }
